@import url("https://fonts.googleapis.com/css?family=Ubuntu+Mono");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: none;
  border: none;
  text-decoration: none;
}
body {
  background-color: #171717;
  /* color: #171717; */
  color: #f2f2f2;
  font-family: "Ubuntu Mono", monospace;
  font-size: 11pt;
  letter-spacing: 1px;
  line-height: 1.6;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}
.terminal {
  width: 60vw;
  height: 80vh;
  overflow-y: scroll;
  /* background-color: #e8e8e8; */
  background-color: #300a24;
  border-radius: 8px;
  box-shadow: 20px 20px 60px #161616;
}
.terminal::-webkit-scrollbar {
  display: none;
}
.header {
  position: fixed;
  display: flex;
  align-items: center;
  width: 60vw;
  height: 40px;
  /* background-color: #c8c8c8; */
  background-color: #1e1e1e;
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
  padding: 0px 8px;
}
.toolbar {
  position: absolute;
  display: flex;
}
.btn {
  border-radius: 7px;
  width: 14px;
  height: 14px;
  display: inline-block;
  background-color: #373737;
  color: transparent;
  margin: 4px;
  cursor: pointer;
}
.btn-1 {
  background-color: #ff3c41;
}
.title {
  margin: auto;
}
.content {
  padding: 0 5px;
}
.group {
  display: flex;
}
input {
  font-family: "Ubuntu Mono", monospace;
  font-size: 11pt;
  letter-spacing: 1px;
  line-height: 1.6;
  background-color: transparent;
  /* color: #171717; */
  color: #ffffff;
  width: 100%;
}
.output {
  white-space: pre-line;
}
.user {
  color: #6a9e41;
}
.dir {
  color: #4a6bab;
}
p {
  margin-top: 40px;
}
a {
  color: #ffffff;
}
a:hover {
  text-decoration: underline;
}
dl {
  color: #aaaaaa;
}
/* Media Queries */
@media (max-width: 768px) {
  .terminal {
    width: 100vw;
    height: 100vh;
    border-radius: 0;
  }
  .header {
    width: 100vw;
    border-radius: 0;
  }
  td {
    vertical-align: top;
  }
}
